export default {
    layout: "Dum-content",
    mounted() {
        this.getblog();
    },
    data() {
        return {
            blogs: [],
            blog: {
                title: "",
                description: "",
                new_image: "",

            },
            errors: {},
            base_url: process.env.url
        };
    },
    methods: {
        getblog() {
            this.$axios
                .$get("/blog/show")
                .then((res) => {
                    this.blogs = res;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        blogEdit(id) {
            $("#blogUpdate").modal("show");
            this.$axios
                .$get("/blog/edit/" + id)
                .then((res) => {
                    this.blog = res;

                })
                .catch((err) => {
                    console.log(err);
                });

        },
        blogUpdate() {
            let formData = new FormData();
            formData.append('title', this.blog.title)
            formData.append('description', this.blog.description)
            if (this.blog.new_image) {
                formData.append('image', this.blog.new_image)
            }
            this.$axios
                .$post("/blog/update/" + this.blog.id, formData)
                .then((res) => {
                    this.getblog();
                    $("#blogUpdate").modal("hide");
                    this.$toaster.success(res.message);
                    this.errors = "";
                })
                .catch((err) => {
                    console.log(err);
                    this.errors = err.response.data.errors;
                });
        },
        deleteblog(id) {
            if (confirm("Are you sure to delete this blog?")) {
                this.$axios
                    .$get("/blog/delete/" + id)
                    .then((res) => {
                        this.getblog();
                        this.$toaster.error(res.message);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }

        },
        blogStatus(id) {
            this.$axios
                .$get("/blog/status/" + id)
                .then((res) => {
                    this.getblog();
                    this.$toaster.success(res.message);
                })
                .catch((err) => {
                    console.log(err);
                });
        },









    }
};
